.headerStyle {
  color: white;
}

.headerStyle img {
  width: 100px;
  height: 30px;
  margin-top: -10px;
  margin-left: 40px;
  margin-right: 40px;
  opacity: 1;
  background-size: cover;
}

.headerStyle .name {
  display: inline-block;
  font-size: 25px;
  margin: 5px 40px;
}

.headerStyle .title {
  font-size: 20px;
  list-style: none;
  display: inline-block;
}

.headerStyle .title li {
  display: inline-block;
  margin-left: 100px;
}

.headerStyle .title li a {
  color: white;
}

.headerStyle .title li a:hover {
  color: black;
}

.headerStyle .button {
  position: fixed;
  top: 20px;
  right: 20px;
  color: black;
  background: white;
  border: 1px solid white;
}